import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage, ResponseData} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {IAcquisitionCampaignDetail, IAcquisitionCampaignReq} from "../models/acquisitioncampaign.model";
import {SalesEndpoints} from "../../sales.endpoints";
import {IAcquisitionCampaign} from "../../leads/models/lead.model";

@Injectable({
  providedIn: 'root'
})
export class AcquisitionCampaignService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = SalesEndpoints.ACQUISITIONCAMPAIGNS.SEARCH(endpoint, params)
    this.http.get<ResponseData<IPage<IAcquisitionCampaign>>>(url)
      .subscribe((data: ResponseData<IPage<IAcquisitionCampaign>>) => {
        this.eventBusService.publish(key, data.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, endpoint: string, id: number) {
    this.http.get<ResponseData<IAcquisitionCampaign>>(SalesEndpoints.ACQUISITIONCAMPAIGNS.GET(endpoint, id))
      .subscribe((data: ResponseData<IAcquisitionCampaign>) => {
        this.eventBusService.publish(key, data.body)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, endpoint, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, endpoint: string, acquisitionCampaignReq: IAcquisitionCampaignReq, exId: number | null) {
    let url = exId == null ? SalesEndpoints.ACQUISITIONCAMPAIGNS.CREATE(endpoint)
      : SalesEndpoints.ACQUISITIONCAMPAIGNS.UPDATE(endpoint, exId)

    let observable = exId == null ?
      this.http.post<ResponseData<IAcquisitionCampaignDetail>>(url, acquisitionCampaignReq)
      : this.http.patch<ResponseData<IAcquisitionCampaignDetail>>(url, acquisitionCampaignReq)

    observable.subscribe(res => {
      this.eventBusService.publish(key, res.body)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, endpoint, acquisitionCampaignReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
