import {Injectable} from "@angular/core";
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage, ResponseData} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {ILead, ILeadDetail, ILeadReq} from "../models/lead.model";
import {SalesEndpoints} from "../../sales.endpoints";

@Injectable({
  providedIn: 'root'
})
export class LeadService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = SalesEndpoints.LEADS.SEARCH(endpoint, params)
    this.http.get<ResponseData<IPage<ILead>>>(url)
      .subscribe((data: ResponseData<IPage<ILead>>) => {
        this.eventBusService.publish(key, data.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchFollowUps(key: string, params: IParams) {
    let url = SalesEndpoints.LEADS.FOLLOW_UPS(params)
    this.http.get<ResponseData<IPage<ILead>>>(url)
      .subscribe((data: ResponseData<IPage<ILead>>) => {
        this.eventBusService.publish(key, data.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchFollowUps(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, endpoint: string, id: number) {
    this.http.get<ResponseData<ILead>>(SalesEndpoints.LEADS.GET(endpoint, id))
      .subscribe((data: ResponseData<ILead>) => {
        this.eventBusService.publish(key, data.body)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, endpoint, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, endpoint: string, leadReq: ILeadReq, exId: number | null) {
    let url = exId == null ? SalesEndpoints.LEADS.CREATE(endpoint)
      : SalesEndpoints.LEADS.UPDATE(endpoint, exId)

    let observable = exId == null ?
      this.http.post<ResponseData<ILeadDetail>>(url, leadReq)
      : this.http.patch<ResponseData<ILeadDetail>>(url, leadReq)

    observable.subscribe(res => {
      this.eventBusService.publish(key, res.body)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, endpoint, leadReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
