import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ILeadStage, ILeadStageDetail} from "../../models/leadstage.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {LeadStageService} from "../../services/leadstage.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {LeadStageUpsertComponent} from "../leadstage-upsert/leadstage-upsert.component";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {selfEndpoints} from "../../models/config.model";
import {BaseSelectComponent} from "../../../../../../../base/components/base-select.component";
import {OrganizationService} from "../../../../organizations/services/organization.service";


@Component({
  selector: 'app-leadstage-select',
  templateUrl: './leadstage-select.component.html',
  styleUrls: ['./leadstage-select.component.scss']
})
export class LeadStageSelectComponent extends BaseSelectComponent {
  @Input() enableAdd: boolean = true

  @Input() searchEndpoint: string = selfEndpoints.endpoint

  items: ILeadStage[] = []
  @Input() selectedItem: ILeadStage | null = null
  @Output() selected: EventEmitter<ILeadStage | null> = new EventEmitter<ILeadStage | null>()

  protected override componentName(): string {
    return "LeadStageSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private leadStageService: LeadStageService,
              private popupService: DialogService<ILeadStageDetail>,
              orgService: OrganizationService) {
    super(eventBus, router, subContainer, orgService)

    if (authService.isAdmin()) {
      this.searchEndpoint = selfEndpoints.adminEndpoint
    }
  }

  override ngOnInit(): void {
    this.loading = false
    this.onOrgLoaded((orgId: number) => {
      this.search({
        page: 0, size: 10, q: null, org_id: orgId
      })
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query, org_id: this.orgId
      })
    }
  }

  onSelected(event: any) {
    if (event == null) {
      this.selected.emit(null)
    } else if (this.items.find(c => c.title == event.title)) {
      this.selected.emit(this.selectedItem)
    } else {
      let popup = this.popupService.open(
        LeadStageUpsertComponent, {id: null, dialogUpsert: true, title: event.title}
      )
      popup.componentInstance.onCompleted.subscribe((leadstage: ILeadStage | null) => {
        this.selected.emit(leadstage)
      })
      popup.componentInstance.onCanceled.subscribe(() => {
        popup.close()
      })
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.STAGES.SEARCH(params))
    this.leadStageService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.items = meta.data.content
      this.loading = false
    })
  }
}
