import {Component, Inject} from '@angular/core';
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {
  AcquisitionCampaignConfig,
  defaultAcquisitionCampaignConfig,
  selfEndpoints,
  selfRoutes
} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AcquisitionCampaignService} from "../../services/acquisitioncampaign.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {IAcquisitionCampaignDetail, IAcquisitionCampaignReq} from "../../models/acquisitioncampaign.model";
import {IOrganization} from "../../../../organizations/models/organization.model";
import {OrganizationService} from "../../../../organizations/services/organization.service";


@Component({
  selector: 'app-acquisitioncampaign-upsert',
  templateUrl: './acquisitioncampaign-upsert.component.html',
  styleUrls: ['./acquisitioncampaign-upsert.component.scss']
})
export class AcquisitionCampaignUpsertComponent extends BaseUpsertComponentV3<IAcquisitionCampaignDetail> {

  config: AcquisitionCampaignConfig = defaultAcquisitionCampaignConfig

  acquisitionCampaignForm = new FormGroup({
    title: new FormControl('', [Validators.required, Validators.minLength(3)]),
    code: new FormControl<string | null>(null, []),
    active: new FormControl<boolean | null>(true, []),
    description: new FormControl('', [Validators.required, Validators.minLength(10)]),
    org_id: new FormControl<number | null>(null, [Validators.required]),
    // image: new FormControl<string | null>(null)
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private acquisitionCampaignService: AcquisitionCampaignService,
              private notifier: NotifierService,
              authService: AuthService,
              private orgService: OrganizationService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (authService.isAdmin()) {
      this.config.endpoint = selfEndpoints.adminEndpoint
      this.config.delEndpoint = selfEndpoints.adminEndpoint
    }

    if (dialogData.title != null) {
      this.acquisitionCampaignForm.controls.title.setValue(dialogData.title)
    }

  }

  protected override componentName(): string {
    return AcquisitionCampaignUpsertComponent.name
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "AcquisitionCampaigns",
        link: "/dash/acquisitioncampaigns",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override ngOnInit() {
    super.ngOnInit();
    let key = this.componentKey(EventKey.ORGANIZATIONS.GET_MY_ORG())
    this.orgService.fetchMyOrgs(key)
    this.subscribe(key, null, (value, meta) => {
      let orgs = meta.data as IOrganization[]
      if (orgs.length > 0) {
        this.acquisitionCampaignForm.controls.org_id.setValue(orgs[0].id)
      }
    })
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.ACQUISITIONCAMPAIGNS.GET(exId))
    this.acquisitionCampaignService.fetchSingle(key, this.config.endpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.acquisitionCampaignForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  // onImgUploaded(images: IImageSlice[]) {
  //   if (images.length > 0) {
  //     /*
  //     Think about the thumbnail, this will only upload the image, not thumbnail
  //      */
  //     this.acquisitionCampaignForm.controls.image.setValue(images[0].image_url)
  //   }
  // }
  //
  // getExImages(): IImageSlice[] {
  //   let image = this.acquisitionCampaignForm.controls.image.value
  //   if (image == null) {
  //     return []
  //   }
  //   return [{image_url: image, thumb_url: image}]
  // }

  getConfig(): BaseConfigV2 {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.ACQUISITIONCAMPAIGNS.UPSERT(exId))
    let req = this.acquisitionCampaignForm.value as IAcquisitionCampaignReq
    this.acquisitionCampaignService.upsert(key, this.config.endpoint, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Campaign has been " + (exId ? "updated" : "created"))
      this.acquisitionCampaignForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IAcquisitionCampaignDetail) {
    this.acquisitionCampaignForm.controls.title.setValue(n.title)
    this.acquisitionCampaignForm.controls.code.setValue(n.code)
    this.acquisitionCampaignForm.controls.active.setValue(n.active)
    this.acquisitionCampaignForm.controls.description.setValue(n.description)
    // this.acquisitionCampaignForm.controls.image.setValue(n.image == null ? null : n.image)
  }

}
