import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {AcquisitionCampaignService} from "../../services/acquisitioncampaign.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {AcquisitionCampaignUpsertComponent} from "../acquisitioncampaign-upsert/acquisitioncampaign-upsert.component";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {selfEndpoints} from "../../models/config.model";
import {IAcquisitionCampaign} from "../../../leads/models/lead.model";
import {IAcquisitionCampaignDetail} from "../../models/acquisitioncampaign.model";

@Component({
  selector: 'app-acquisitioncampaign-select',
  templateUrl: './acquisitioncampaign-select.component.html',
  styleUrls: ['./acquisitioncampaign-select.component.scss']
})
export class AcquisitionCampaignSelectComponent extends BaseComponent {
  @Input() enableAdd: boolean = true

  @Input() searchEndpoint: string = selfEndpoints.endpoint

  items: IAcquisitionCampaign[] = []
  @Input() selectedItem: IAcquisitionCampaign | null = null
  @Output() selected: EventEmitter<IAcquisitionCampaign | null> = new EventEmitter<IAcquisitionCampaign | null>()

  protected override componentName(): string {
    return "AcquisitionCampaignSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private acquisitionCampaignService: AcquisitionCampaignService,
              private popupService: DialogService<IAcquisitionCampaignDetail>) {
    super(eventBus, router, subContainer)

    if (authService.isAdmin()) {
      this.searchEndpoint = selfEndpoints.adminEndpoint
    }
  }

  override ngOnInit(): void {
    this.loading = false
    this.search({
      page: 0, size: 10, q: null
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query
      })
    }
  }

  onSelected(event: any) {
    if (event == null) {
      this.selected.emit(null)
    } else if (this.items.find(c => c.title == event.title)) {
      this.selected.emit(this.selectedItem)
    } else {
      let popup = this.popupService.open(
        AcquisitionCampaignUpsertComponent, {id: null, dialogUpsert: true, title: event.title}
      )
      popup.componentInstance.onCompleted.subscribe((acquisitioncampaign: IAcquisitionCampaign | null) => {
        this.selected.emit(acquisitioncampaign)
      })
      popup.componentInstance.onCanceled.subscribe(() => {
        popup.close()
      })
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.ACQUISITIONCAMPAIGNS.SEARCH(params))
    this.acquisitionCampaignService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.items = meta.data.content
      this.loading = false
    })
  }
}
