import {Component, Inject} from "@angular/core";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {defaultLeadStageConfig, LeadStageConfig, selfEndpoints, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {LeadStageService} from "../../services/leadstage.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {ILeadStageDetail, ILeadStageReq} from "../../models/leadstage.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";


@Component({
  selector: 'app-leadstage-upsert',
  templateUrl: './leadstage-upsert.component.html',
  styleUrls: ['./leadstage-upsert.component.scss']
})
export class LeadStageUpsertComponent extends BaseUpsertComponentV3<ILeadStageDetail> {

  config: LeadStageConfig = defaultLeadStageConfig

  leadStageForm = new FormGroup({
    title: new FormControl('', [Validators.required, Validators.minLength(3)]),
    color: new FormControl('#B8BAFF', [Validators.required]),
    default: new FormControl<boolean>(false, []),
    description: new FormControl('', [Validators.required, Validators.maxLength(255)]),
    image: new FormControl<string | null>(null),
    organization_id: new FormControl<number | null>(null, [Validators.required])
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private leadStageService: LeadStageService,
              private notifier: NotifierService,
              authService: AuthService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (authService.isAdmin()) {
      this.config.endpoint = selfEndpoints.adminEndpoint
      this.config.delEndpoint = selfEndpoints.adminEndpoint
    }

    if (dialogData.title != null) {
      this.leadStageForm.controls.title.setValue(dialogData.title)
    }

  }

  override ngOnInit() {
    super.ngOnInit();

    this.leadStageForm.controls.organization_id.setValue(this.selectedOrgId)
  }

  protected override componentName(): string {
    return LeadStageUpsertComponent.name
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "LeadStages",
        link: "/dash/sales/stages",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.STAGES.GET(exId))
    this.leadStageService.fetchSingle(key, this.config.endpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.leadStageForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.leadStageForm.controls.image.setValue(images[0].image_url)
    }
  }

  getExImages(): IImageSlice[] {
    let image = this.leadStageForm.controls.image.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getConfig(): BaseConfigV2 {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.STAGES.UPSERT(exId))
    let req = this.leadStageForm.value as ILeadStageReq
    this.leadStageService.upsert(key, this.config.endpoint, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "LeadStage has been " + (exId ? "updated" : "created"))
      this.leadStageForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: ILeadStageDetail) {
    this.leadStageForm.controls.title.setValue(n.title)
    this.leadStageForm.controls.color.setValue(n.color)
    this.leadStageForm.controls.description.setValue(n.description)
    this.leadStageForm.controls.default.setValue(n.default)
    this.leadStageForm.controls.image.setValue(n.image == null ? null : n.image)
    this.leadStageForm.controls.organization_id.setValue(n.organization.id)
  }

}
