import {Component} from '@angular/core';
import {BaseListComponentV4} from "../../../../../../../base/components/base-list-v4.component";
import {defaultLeadConfig, LeadConfig, selfEndpoints, selfRoutes} from "../../models/config.model";
import {
  BasicFilterData, defaultFilterData,
  parseFilterData, toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {LeadService} from "../../services/lead.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {ComponentType} from "@angular/cdk/overlay";
import {LeadUpsertComponent} from "../lead-upsert/lead-upsert.component";
import {ILead, ILeadDetail} from "../../models/lead.model";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {ILeadPropagationDetail} from "../../../leadpropagations/models/leadpropagation.model";
import {
  LeadPropagationUpsertComponent
} from "../../../leadpropagations/components/leadpropagation-upsert/leadpropagation-upsert.component";
import {ReactionViewComponent} from "../../../../../../webapp/components/social/reaction-view/reaction-view.component";
import {
  CrudExampleUpsertComponent
} from "../../../../crudexample/components/crudexample-upsert/crudexample-upsert.component";
import {ICrudExample} from "../../../../crudexample/models/crudexample.model";
import {BaseListComponentForOrg} from "../../../../organizations/components/org-base-list.component";
import {OrganizationService} from "../../../../organizations/services/organization.service";
import {defaultSalesOverview} from "../../../../../pages/dashboards/sales/models/stats.model";
import {take} from "rxjs";
import {TimeUtility} from "../../../../../../common/modules/utility/time.utility";
import {translations} from "../../../../../layout/left-nav/menu.model";

@Component({
  selector: 'app-lead-list',
  templateUrl: './lead-list.component.html',
  styleUrls: ['./lead-list.component.scss']
})
export class LeadListComponent extends BaseListComponentForOrg<ILead, ILeadDetail> {

  config: LeadConfig = defaultLeadConfig
  filterData: BasicFilterData = defaultFilterData

  leads: IPage<ILead> = defaultPage
  followUps: IPage<ILead> = defaultPage

  currentDate = new Date()

  protected componentName(): string {
    return "LeadListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<ILeadDetail>,
              private activatedRoute: ActivatedRoute,
              private leadService: LeadService,
              private notifier: NotifierService,
              private lpDialog: DialogService<ILeadPropagationDetail>,
              orgService: OrganizationService
  ) {
    super(eventBus, router, subContainer, dialog, orgService)
    if (authService.isAdmin()) {
      this.config.endpoint = selfEndpoints.adminEndpoint
      this.config.delEndpoint = selfEndpoints.adminEndpoint
    }
    if (authService.isAdmin() || authService.hasPrivilege('MODIFY_ORG_CONFIGURATION')) {
      this.config.showDeleteButton = true
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)

      this.onOrgLoaded((orgId) => {
        this.fetchLeads({
          q: this.filterData.query,
          username: this.filterData.username,
          from_date: this.filterData.fromDate,
          to_date: this.filterData.toDate,
          page: this.filterData.page,
          size: this.filterData.size,
          assignee_id: this.filterData.extra['assignee_id'],
          stage_id: this.filterData.extra['stage_id'],
          org_id: orgId
        })

        this.fetchFollowUps({
          q: this.filterData.query,
          username: this.filterData.username,
          from_date: TimeUtility.addDays(new Date(), -30),
          to_date: TimeUtility.addDays(new Date(), 30),
          page: this.filterData.page,
          size: this.filterData.size,
          assignee_id: this.filterData.extra['assignee_id'],
          stage_id: this.filterData.extra['stage_id'],
          org_id: orgId

        })
      })
    });

  }


  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: translations["Leads"],
        link: null,
        active: true
      }
    ]
  }

  private fetchLeads(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LEADS.SEARCH(params))
    this.leadService.search(key, this.config.endpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.leads = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  private fetchFollowUps(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LEADS.FOLLOW_UPS(params))
    this.leadService.fetchFollowUps(key, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.followUps = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Lead is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfigV2 {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV3<ILeadDetail>> | null {
    return LeadUpsertComponent;
  }

  onPropagationBtnClick(item: ILead) {
    let popup = this.lpDialog.open(
      LeadPropagationUpsertComponent, {id: null, lead: item, dialogUpsert: true}
    )
    popup.componentInstance.onCompleted.subscribe((p: ILeadPropagationDetail | null) => {
      this.ngOnInit()
    })
    popup.componentInstance.onCanceled.subscribe(() => {
      popup.close()
    })
  }

}
