import {Component, Inject, LOCALE_ID} from '@angular/core';
import {BaseListComponentV4} from "../../../../../../../base/components/base-list-v4.component";
import {
  AcquisitionCampaignConfig,
  defaultAcquisitionCampaignConfig,
  selfEndpoints,
  selfRoutes
} from "../../models/config.model";
import {
  BasicFilterData, defaultFilterData,
  parseFilterData, toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {AcquisitionCampaignService} from "../../services/acquisitioncampaign.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {AcquisitionCampaignUpsertComponent} from "../acquisitioncampaign-upsert/acquisitioncampaign-upsert.component";
import {IAcquisitionCampaign, IAcquisitionCampaignDetail} from "../../models/acquisitioncampaign.model";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {Location} from "@angular/common";

@Component({
  selector: 'app-acquisitioncampaign-list',
  templateUrl: './acquisitioncampaign-list.component.html',
  styleUrls: ['./acquisitioncampaign-list.component.scss']
})
export class AcquisitionCampaignListComponent extends BaseListComponentV4<IAcquisitionCampaign, IAcquisitionCampaignDetail> {

  config: AcquisitionCampaignConfig = defaultAcquisitionCampaignConfig
  filterData: BasicFilterData = defaultFilterData

  acquisitionCampaigns: IPage<IAcquisitionCampaign> = defaultPage

  protected componentName(): string {
    return AcquisitionCampaignListComponent.name;
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<IAcquisitionCampaignDetail>,
              private activatedRoute: ActivatedRoute,
              private acquisitionCampaignService: AcquisitionCampaignService,
              private notifier: NotifierService,
              @Inject(LOCALE_ID) private locale: string,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.showUserFilter = true
      this.config.endpoint = selfEndpoints.adminEndpoint
      this.config.delEndpoint = selfEndpoints.adminEndpoint
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      this.fetchAcquisitionCampaigns({
        q: this.filterData.query,
        username: this.filterData.username,
        from_date: this.filterData.fromDate,
        to_date: this.filterData.toDate,
        page: this.filterData.page,
        size: this.filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "AcquisitionCampaigns",
        link: null,
        active: true
      }
    ]
  }

  private fetchAcquisitionCampaigns(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.ACQUISITIONCAMPAIGNS.SEARCH(params))
    this.acquisitionCampaignService.search(key, this.config.endpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.acquisitionCampaigns = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "AcquisitionCampaign is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfigV2 {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV3<IAcquisitionCampaignDetail>> | null {
    return AcquisitionCampaignUpsertComponent;
  }

  onCopyClick(code: string) {
    TextUtility.copyToClipboard(window.location.hostname + "/" + this.locale + "/#/aq-campaigns/" + code + "/acquire")
    this.notifier.info("Success", "Code Copied")
  }
}
