import {BaseComponent} from "./base.component";
import {LocalStorageService} from "../../services/storage/localstorage.service";
import {Component} from "@angular/core";
import {EventKey} from "../../services/eventbus/event.key";
import {IOrganization} from "../../modules/dash/domains/organizations/models/organization.model";
import {EventBusService} from "../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../services/subscription-container.service";
import {OrganizationService} from "../../modules/dash/domains/organizations/services/organization.service";
import {DialogService} from "../../modules/common/modules/utility/components/popup/dialog.service";

@Component({
  template: ''
})
export abstract class BaseSelectComponent extends BaseComponent {

  orgId: number | null = null

  protected constructor(eventBus: EventBusService,
                        router: Router,
                        subContainer: SubscriptionContainerService,
                        private orgService: OrganizationService
  ) {
    super(eventBus, router, subContainer)
  }


  protected onOrgLoaded(block: (orgId: number) => void) {
    this.initOrg()
    if (this.orgId != null) {
      block(this.orgId)
    } else {
      this.fetchOrgId(block)
    }
  }

  protected initOrg() {
    let orgStr = LocalStorageService.retrieve('selectedOrg')
    let org = orgStr != undefined ? JSON.parse(orgStr) : null
    if (org != null) {
      this.orgId = org.id
    }
  }

  fetchOrgId(block: (orgId: number) => void) {
    let key = this.componentKey(EventKey.ORGANIZATIONS.GET_MY_ORG())
    this.orgService.fetchMyOrgs(key)
    this.subscribe(key, null, (value, meta) => {
      let orgs = meta.data as IOrganization[]
      if (orgs.length > 0) {
        block(orgs[0].id)
      }
    })
  }
}
