import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage, ResponseData} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {
  IEmployeeActivity,
  ILeadPropagation,
  ILeadPropagationDetail,
  ILeadPropagationReq
} from "../models/leadpropagation.model";
import {SalesEndpoints} from "../../sales.endpoints";

@Injectable({
  providedIn: 'root'
})
export class LeadPropagationService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = SalesEndpoints.LEADPROPAGATIONS.SEARCH(endpoint, params)
    this.http.get<ResponseData<IPage<ILeadPropagation>>>(url)
      .subscribe((data: ResponseData<IPage<ILeadPropagation>>) => {
        this.eventBusService.publish(key, data.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, endpoint: string, id: number) {
    this.http.get<ResponseData<ILeadPropagation>>(SalesEndpoints.LEADPROPAGATIONS.GET(endpoint, id))
      .subscribe((data: ResponseData<ILeadPropagation>) => {
        this.eventBusService.publish(key, data.body)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, endpoint, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, endpoint: string, leadPropagationReq: ILeadPropagationReq, exId: number | null) {
    let url = exId == null ? SalesEndpoints.LEADPROPAGATIONS.CREATE(endpoint)
      : SalesEndpoints.LEADPROPAGATIONS.UPDATE(endpoint, exId)

    let observable = exId == null ?
      this.http.post<ResponseData<ILeadPropagationDetail>>(url, leadPropagationReq)
      : this.http.patch<ResponseData<ILeadPropagationDetail>>(url, leadPropagationReq)

    observable.subscribe(res => {
      this.eventBusService.publish(key, res.body)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, endpoint, leadPropagationReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

  fetchEmployeeActivities(key: string, params: IParams) {
    let url = SalesEndpoints.LEADPROPAGATIONS.EMPLOYEE_ACTIVITIES(params)
    this.http.get<ResponseData<IEmployeeActivity[]>>(url)
      .subscribe((data: ResponseData<IEmployeeActivity[]>) => {
        this.eventBusService.publish(key, data.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchEmployeeActivities(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }
}
