import {Component, Inject} from "@angular/core";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {
  defaultLeadPropagationConfig,
  LeadPropagationConfig,
  selfEndpoints,
  selfRoutes
} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {LeadPropagationService} from "../../services/leadpropagation.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {ILeadPropagationDetail, ILeadPropagationReq} from "../../models/leadpropagation.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {OrganizationService} from "../../../../organizations/services/organization.service";
import {ILead} from "../../../leads/models/lead.model";
import {IOrganization} from "../../../../organizations/models/organization.model";
import {TimeUtility} from "../../../../../../common/modules/utility/time.utility";
import {ILeadStage} from "../../../leadstages/models/leadstage.model";


@Component({
  selector: 'app-leadpropagation-upsert',
  templateUrl: './leadpropagation-upsert.component.html',
  styleUrls: ['./leadpropagation-upsert.component.scss']
})
export class LeadPropagationUpsertComponent extends BaseUpsertComponentV3<ILeadPropagationDetail> {

  config: LeadPropagationConfig = defaultLeadPropagationConfig
  readonly = false

  selectedStage: ILeadStage | null = null
  selectedLead: ILead | null = null
  currentDate = new Date()

  leadPropagationForm = new FormGroup({
    stage_to: new FormControl<number | null>(null, [Validators.required]),
    image: new FormControl<string | null>(null),
    lead_id: new FormControl<number | null>(null, [Validators.required]),
    org_id: new FormControl<number | null>(null, [Validators.required]),
    follow_up_date: new FormControl<Date | null>(null),
    comment: new FormGroup({
      content: new FormControl<string>("", [Validators.required, Validators.minLength(10)]),
      type: new FormControl<string>('INTERNAL')
    })
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private leadPropagationService: LeadPropagationService,
              private notifier: NotifierService,
              authService: AuthService,
              private orgService: OrganizationService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (authService.isAdmin()) {
      this.config.endpoint = selfEndpoints.adminEndpoint
      this.config.delEndpoint = selfEndpoints.adminEndpoint
    }

    if (dialogData.lead_id != null) {
      this.leadPropagationForm.controls.lead_id.setValue(dialogData.lead_id)
    }
    if (dialogData.lead != null) {
      this.selectedLead = dialogData.lead
      this.selectedStage = this.selectedLead == null ? null : this.selectedLead.stage
      this.leadPropagationForm.controls.lead_id.setValue(
        this.selectedLead == null ? null : this.selectedLead.id
      )
      this.leadPropagationForm.controls.stage_to.setValue(this.selectedStage == null ? null : this.selectedStage.id)
      this.readonly = true
    }

  }

  override ngOnInit() {
    super.ngOnInit();

    let key = this.componentKey(EventKey.ORGANIZATIONS.GET_MY_ORG())
    this.orgService.fetchMyOrgs(key)
    this.subscribe(key, null, (value, meta) => {
      let orgs = meta.data as IOrganization[]
      if (orgs.length > 0) {
        this.leadPropagationForm.controls.org_id.setValue(orgs[0].id)
      }
    })
  }

  protected override componentName(): string {
    return LeadPropagationUpsertComponent.name
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Activities",
        link: "/dash/lead-propagations",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.LEADPROPAGATIONS.GET(exId))
    this.leadPropagationService.fetchSingle(key, this.config.endpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.leadPropagationForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.leadPropagationForm.controls.image.setValue(images[0].image_url)
    }
  }

  getExImages(): IImageSlice[] {
    let image = this.leadPropagationForm.controls.image.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getConfig(): BaseConfigV2 {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.LEADPROPAGATIONS.UPSERT(exId))
    let req = this.leadPropagationForm.value as ILeadPropagationReq
    req.follow_up_date = req.follow_up_date == null ? null : TimeUtility.toISO(req.follow_up_date)
    this.leadPropagationService.upsert(key, this.config.endpoint, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "LeadPropagation has been " + (exId ? "updated" : "created"))
      this.leadPropagationForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: ILeadPropagationDetail) {
    // this.leadPropagationForm.controls.title.setValue(n.title)
    // this.leadPropagationForm.controls.description.setValue(n.description)
    // this.leadPropagationForm.controls.image.setValue(n.image == null ? null : n.image)
  }

  onStageSelected($event: any) {
    this.selectedStage = $event
    this.leadPropagationForm.controls.stage_to.setValue($event.id)
  }

  onLeadSelected($event: ILead | null) {
    this.selectedLead = $event
    this.leadPropagationForm.controls.lead_id.setValue(
      $event == null ? null : $event.id
    )
  }
}
