import {Component} from '@angular/core';
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {convertDateFieldsToISO} from "../../../../../../../base/endpoints/iparams";
import {
  BasicFilterComponent
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.component";
import {ICustomer} from "../../../customers/models/customer.model";

@Component({
  selector: 'app-order-filter',
  templateUrl: './order-filter.component.html',
  styleUrls: ['./order-filter.component.scss']
})
export class OrderFilterComponent extends BasicFilterComponent {
  selectedCustomer: ICustomer | null = null

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private route: ActivatedRoute,
               authService: AuthService) {
    super(eventBus, router, subContainer, route, authService)
  }

  protected override componentName(): string {
    return OrderFilterComponent.name;
  }

  override onFilterClick() {
    if (!this.useParamNavigation) {
      this.filterClick.emit(this.filterData)
    } else {
      this.router.navigate(['.'], {
        relativeTo: this.activatedRoute,
        queryParams: {
          username: this.filterData.username,
          query: this.filterData.query,
          from_date: this.filterData.fromDate.toISOString(),
          to_date: this.filterData.toDate.toISOString(),
          local_customer_id: this.selectedCustomer?.id,
          page: this.filterData.page,
          size: this.filterData.size,
        },
        queryParamsHandling: 'merge',
      }).then(r => {
      });
    }
  }

  override clearFilter() {
    this.selectedCustomer = null
    super.clearFilter();
  }

  onCustomerSelected(event: ICustomer | null) {
    this.selectedCustomer = event
  }
}
