import {Component, Inject} from '@angular/core';
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {defaultLeadConfig, LeadConfig, selfEndpoints, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {LeadService} from "../../services/lead.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {ILeadDetail, ILeadReq} from "../../models/lead.model";
import {OrganizationService} from "../../../../organizations/services/organization.service";
import {IEmployee} from "../../../../hr/employees/models/employee.model";
import {IOrganization} from "../../../../organizations/models/organization.model";
import {ILeadStage} from "../../../leadstages/models/leadstage.model";


@Component({
  selector: 'app-lead-upsert',
  templateUrl: './lead-upsert.component.html',
  styleUrls: ['./lead-upsert.component.scss']
})
export class LeadUpsertComponent extends BaseUpsertComponentV3<ILeadDetail> {

  config: LeadConfig = defaultLeadConfig

  selectedStage: ILeadStage | null = null
  selectedAssignee: IEmployee | null = null

  leadForm = new FormGroup({
    title: new FormControl('', [Validators.required, Validators.minLength(3)]),
    description: new FormControl<string | null>(null),
    image: new FormControl<string | null>(null),
    phone: new FormControl<string | null>(null),
    email: new FormControl<string | null>(null, [Validators.email]),
    stage_id: new FormControl<number | null>(null, [Validators.required]),
    assignee_id: new FormControl<number | null>(null),
    org_id: new FormControl<number | null>(null, [Validators.required]),
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private leadService: LeadService,
              private notifier: NotifierService,
              private orgService: OrganizationService,
              authService: AuthService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (authService.isAdmin()) {
      this.config.showUserFilter = true
      this.config.endpoint = selfEndpoints.adminEndpoint
      this.config.delEndpoint = selfEndpoints.adminEndpoint
    }

    if (dialogData.title != null) {
      this.leadForm.controls.title.setValue(dialogData.title)
    }

  }

  protected override componentName(): string {
    return LeadUpsertComponent.name
  }

  override ngOnInit() {
    super.ngOnInit();

    let key = this.componentKey(EventKey.ORGANIZATIONS.GET_MY_ORG())
    this.orgService.fetchMyOrgs(key)
    this.subscribe(key, null, (value, meta) => {
      let orgs = meta.data as IOrganization[]
      if (orgs.length > 0) {
        this.leadForm.controls.org_id.setValue(orgs[0].id)
      }
    })
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Leads",
        link: "/dash/leads",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.LEADS.GET(exId))
    this.leadService.fetchSingle(key, this.config.endpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.leadForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.leadForm.controls.image.setValue(images[0].image_url)
    }
  }

  getExImages(): IImageSlice[] {
    let image = this.leadForm.controls.image.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getConfig(): BaseConfigV2 {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.LEADS.UPSERT(exId))
    let req = this.leadForm.value as ILeadReq
    this.leadService.upsert(key, this.config.endpoint, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Lead has been " + (exId ? "updated" : "created"))
      this.leadForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: ILeadDetail) {
    this.selectedAssignee = n.assignee
    this.selectedStage = n.stage

    this.leadForm.controls.title.setValue(n.title)
    this.leadForm.controls.description.setValue(n.description)
    this.leadForm.controls.image.setValue(n.image == null ? null : n.image)
    this.leadForm.controls.phone.setValue(n.phone == null ? null : n.phone)
    this.leadForm.controls.email.setValue(n.email == null ? null : n.email)
    this.leadForm.controls.stage_id.setValue(n.stage.id)
    this.leadForm.controls.assignee_id.setValue(n.assignee == null ? null : n.assignee.id)
    this.leadForm.controls.org_id.setValue(n.organization.id)
  }

  onStageSelect($event: ILeadStage | null) {
    this.selectedStage = $event
    this.leadForm.controls.stage_id.setValue(
      this.selectedStage == null ? null : this.selectedStage.id
    )
  }

  onAssigneeSelected($event: IEmployee | null) {
    this.selectedAssignee = $event
    this.leadForm.controls.assignee_id.setValue(
      this.selectedAssignee == null ? null : this.selectedAssignee.id
    )
  }
}
