import {NgModule} from '@angular/core';
import {RouterModule, Routes} from "@angular/router";
import {OrderComponent} from "./orders/order.component";
import {OrderUpsertComponent} from "./orders/components/order-upsert/order-upsert.component";
import {CustomerComponent} from "./customers/customer.component";
import {CustomerUpsertComponent} from "./customers/components/customer-upsert/customer-upsert.component";
import {LeadComponent} from "./leads/lead.component";
import {LeadUpsertComponent} from "./leads/components/lead-upsert/lead-upsert.component";
import {LeadPropagationComponent} from "./leadpropagations/leadpropagation.component";
import {
  LeadPropagationUpsertComponent
} from "./leadpropagations/components/leadpropagation-upsert/leadpropagation-upsert.component";
import {LeadStageComponent} from "./leadstages/leadstage.component";
import {LeadStageUpsertComponent} from "./leadstages/components/leadstage-upsert/leadstage-upsert.component";
import {AcquisitionCampaignComponent} from "./acquisitioncampaigns/acquisitioncampaign.component";
import {
  AcquisitionCampaignUpsertComponent
} from "./acquisitioncampaigns/components/acquisitioncampaign-upsert/acquisitioncampaign-upsert.component";

const routes: Routes = [
  {path: "orders", component: OrderComponent},
  {path: "orders/create", component: OrderUpsertComponent},
  {path: "orders/:id/update", component: OrderUpsertComponent},
  {path: "customers", component: CustomerComponent},
  {path: "customers/create", component: CustomerUpsertComponent},
  {path: "customers/:id/update", component: CustomerUpsertComponent},
  {path: "stages", component: LeadStageComponent},
  {path: "stages/create", component: LeadStageUpsertComponent},
  {path: "stages/:id/update", component: LeadStageUpsertComponent},
  {path: "leads", component: LeadComponent},
  {path: "leads/create", component: LeadUpsertComponent},
  {path: "leads/:id/update", component: LeadUpsertComponent},
  {path: "lead-propagations", component: LeadPropagationComponent},
  {path: "lead-propagations/create", component: LeadPropagationUpsertComponent},
  {path: "lead-propagations/:id/update", component: LeadPropagationUpsertComponent},
  {path: "acquisition-campaigns", component: AcquisitionCampaignComponent},
  {path: "acquisition-campaigns/create", component: AcquisitionCampaignUpsertComponent},
  {path: "acquisition-campaigns/:id/update", component: AcquisitionCampaignUpsertComponent},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SalesRoute {
}
